/* App.css */

html,
body,
#root {
  height: 100%;
  margin: 0;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: #302e2b;
  background-size: cover;
  text-align: center;
  flex-direction: column;
  flex-wrap: wrap;
}

.ButtonContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}